<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('yinshijilu','首页总数')">
				<el-card v-show="countTypeList.closeyinshijiluCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								饮食记录
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenyinshijiluCountType')" class="showIcons"
									:class="countTypeList.hiddenyinshijiluCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closeyinshijiluCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenyinshijiluCountType">
							<div class="count_title">饮食记录总数</div>
							<div class="count_num">{{yinshijiluCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('yinshijilu','首页统计')">
				<el-card v-show="cardTypeList.closeyinshijiluChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								饮食记录
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenyinshijiluChartType1')" class="showIcons"
									:class="cardTypeList.hiddenyinshijiluChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closeyinshijiluChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenyinshijiluChartType1">
							<div id="yinshijilureliangEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('yinshijilu','首页统计')">
				<el-card v-show="cardTypeList.closeyinshijiluChartType2" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								饮食记录
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenyinshijiluChartType2')" class="showIcons"
									:class="cardTypeList.hiddenyinshijiluChartType2?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closeyinshijiluChartType2')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenyinshijiluChartType2">
							<div id="yinshijiludanbaizhiEchart2" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('yinshijilu','首页统计')">
				<el-card v-show="cardTypeList.closeyinshijiluChartType3" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								饮食记录
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenyinshijiluChartType3')" class="showIcons"
									:class="cardTypeList.hiddenyinshijiluChartType3?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closeyinshijiluChartType3')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenyinshijiluChartType3">
							<div id="yinshijiluzhifangEchart3" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closeyinshijiluCountType = true
		countTypeList.value.hiddenyinshijiluCountType = true
		if(btnAuth('yinshijilu','首页总数')){
			getyinshijiluCount()
		}
	}
	const yinshijiluCount = ref(0)
	const getyinshijiluCount = () => {
		context?.$http({
			url:'yinshijilu/count',
			method: 'get'
		}).then(res=>{
			yinshijiluCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closeyinshijiluChartType1: true,
		hiddenyinshijiluChartType1: true,
		closeyinshijiluChartType2: true,
		hiddenyinshijiluChartType2: true,
		closeyinshijiluChartType3: true,
		hiddenyinshijiluChartType3: true,
	})
	const getCardList = () => {
		if(btnAuth('yinshijilu','首页统计')){
			getyinshijiluChart1()
		}
		if(btnAuth('yinshijilu','首页统计')){
			getyinshijiluChart2()
		}
		if(btnAuth('yinshijilu','首页统计')){
			getyinshijiluChart3()
		}
	}
	const getyinshijiluChart1 = () => {
		nextTick(()=>{
			var reliangEchart1 = echarts.init(document.getElementById("yinshijilureliangEchart1"),'macarons');
			context?.$http({
				url: `yinshijilu/value/yonghuxingming/reliang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].yonghuxingming);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].yonghuxingming
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '热量统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				reliangEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				reliangEchart1.setOption(option);
				//根据窗口的大小变动图表
				reliangEchart1.resize();
			})
		})
	}
	const getyinshijiluChart2 = () => {
		nextTick(()=>{
			var danbaizhiEchart2 = echarts.init(document.getElementById("yinshijiludanbaizhiEchart2"),'macarons');
			context?.$http({
				url: `yinshijilu/value/yonghuxingming/danbaizhi`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].yonghuxingming);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].yonghuxingming
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '蛋白质统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				// 使用刚指定的配置项和数据显示图表。
				danbaizhiEchart2.clear()
				danbaizhiEchart2.setOption(option);
				//根据窗口的大小变动图表
				danbaizhiEchart2.resize();
			})
		})
	}
	const getyinshijiluChart3 = () => {
		nextTick(()=>{
        //  zhifang
			var zhifangEchart3 = echarts.init(document.getElementById("yinshijiluzhifangEchart3"),'macarons');
			context?.$http({
				url: `yinshijilu/value/yonghuxingming/zhifang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].yonghuxingming);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].yonghuxingming
				    })
				}
				var option = {};
				option = {
				    title: {
				        text: '脂肪统计',
				        left: 'center'
				    },
				    tooltip: {
				      trigger: 'item',
				      formatter: '{b} : {c}'
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: xAxis
				    },
				    yAxis: {
				        type: 'value'
				    },
				    series: [{
				        data: yAxis,
				        type: 'line',
                    }]
                };
				// 使用刚指定的配置项和数据显示图表。
				zhifangEchart3.clear()
				zhifangEchart3.setOption(option);
				//根据窗口的大小变动图表
				zhifangEchart3.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		margin: 20px 0 20px;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 24px;
		justify-content: center;
		align-items: center;
		height: auto;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #19a97b30;
			border-radius: 0;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			margin: 0 1% 20px;
			width: 48%;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 0px solid #19a97b30;
				background: #19a97b10;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					height: 10px;
					// 标题
					.card_head_title {
						color: #666;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px;
					text-align: center;
					// 总数标题
					.count_title{
						color: #333;
						font-size: 16px;
						line-height: 2;
					}
					// 总数数字
					.count_num{
						color: #c00;
						font-size: 30px;
						line-height: 2;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 0px 20px;
		margin: 0;
		background: url(http://clfile.zggen.cn/20240203/09a98f4852104a599055d47953ef0a23.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 1px solid #19a97b30;
			border-radius: 0;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			margin: 0 1% 30px;
			width: 100%;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 0px solid #19a97b30;
				background: #19a97b10;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					height: 10px;
					// 标题
					.card_head_title {
						color: #f00;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
